						<tr class="Hyzx_not_fist_tr_23" style="display:none">
							<td></td>
							<td>0.00</td>
							<td>20000</td>
							<td>10000</td>
							<td>1</td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		methods: {

		},
		mounted: function() {
			let off = true;
			$('.tzzxSport').click(function(){
				if(off){
					$(this).find('tbody tr').css('display','table-row');
					off = false;
				}else{
					$(this).find('tbody tr').css('display','none');
					off = true
				}
			})
		}
	}
</script>
